window.onload = function () {
  let DATA = {};

  Date.prototype.format = function () {
    // ÌòÑÏû¨ ÎÇ†Ïßú Î≥¥Í∏∞Ï¢ãÍ≤å Ï∂úÎ†• / ÏÇ¨Ïö©Î∞©Î≤ï: newDate().format() ÏúºÎ°ú ÏÇ¨Ïö©Í∞ÄÎä•
    let yyyy = this.getFullYear();
    let month = this.getMonth() + 1;
    let dd = this.getDate();
    let format = [yyyy, month, dd].join("-");
    return format;
  };

  Date.prototype.format2 = function () {
    // ÌòÑÏû¨ ÎÇ†Ïßú Î≥¥Í∏∞Ï¢ãÍ≤å Ï∂úÎ†• / ÏÇ¨Ïö©Î∞©Î≤ï: newDate().format() ÏúºÎ°ú ÏÇ¨Ïö©Í∞ÄÎä•
    let yyyy = this.getFullYear();
    let month = this.getMonth() + 1;
    let format = [yyyy, month].join("-");
    return format;
  };

  let today = new Date();
  const calendarBody = document.querySelector(".calendar-body");
  const prevEl = document.querySelector(".prev");
  const nextEl = document.querySelector(".next");
  const inputBox = document.querySelector(".question");
  const inputBtn = document.querySelector(".input-btn");
  const inputList = document.querySelector(".todoList");
  const showList = document.querySelector(".showList");
  const listText = document.querySelector(".listText");
  const createDate = document.querySelector(".createDate");
  const bgblack = document.querySelector(".bgblack");
  const closedBtn = document.querySelector(".closed");

  let currentDate;

  buildCalendar();
  function buildCalendar() {
    let firstDate = new Date(today.getFullYear(), today.getMonth(), 1);
    const monthList = [
      "1Ïõî",
      "2Ïõî",
      "3Ïõî",
      "4Ïõî",
      "5Ïõî",
      "6Ïõî",
      "7Ïõî",
      "8Ïõî",
      "9Ïõî",
      "10Ïõî",
      "11Ïõî",
      "12Ïõî",
    ];
    const leapYear = [31, 29, 31, 30, 31, 30, 31, 31, 30, 31, 30, 31];
    const notLeapYear = [31, 28, 31, 30, 31, 30, 31, 31, 30, 31, 30, 31];
    const headerYear = document.querySelector(".current-year-month");
    // Ïú§ÎÖÑ Ï≤¥ÌÅ¨ÌïòÍ∏∞
    if (firstDate.getFullYear() % 4 === 0) {
      pageYear = leapYear;
    } else {
      pageYear = notLeapYear;
    }
    headerYear.innerHTML = `Ïò§Îäò`;
    makeElement(firstDate);
    showMain();
    currentDateget();
    resetInsert();
  }

  function makeElement(firstDate) {
    
    let dateSet = 1;
    for (let i = 0; i < 6; i++) {
      for (let j = 0; j < 7; j++) {
        // i === 0Ïù¥Ïó¨Ïïº ÌïòÎäî Ïù¥Ïú†Îäî Ï≤´ ÎÇ†ÏßúÎ•º Ï∞çÍ≥† Í∑∏ Îã§Ïùå ÎÇ†ÏßúÍ∞Ä 0Î≤àÏß∏ Ïπ∏Î∂ÄÌÑ∞ Îã§Ïãú Í∑∏Î†§Ï†∏Ïïº ÌïòÍ∏∞ ÎïåÎ¨∏
        // firstDate.getMonth() => ÌòÑÏû¨ Îã¨Ïùò ÏùºÏàòÍ∞Ä Î™áÏùºÏù∏ÏßÄ Î∞òÌôòÌï¥Ï£ºÍ≥†, Ïù¥ Ï°∞Í±¥ÏùÄ Î∞òÌôò Îêú Í∞íÏóê Îî∞Îùº Ï∂úÎ†•Ìï¥ Ï§Ä ÌõÑ, Îã¨Î†• Ï∂úÎ†• Ï¢ÖÎ£åÏ°∞Í±¥Ïù¥Îã§.
        if (
          (i === 0 && j < firstDate.getDay()) ||
          dateSet > pageYear[firstDate.getMonth()]
        ) {
          // ÎßåÏïΩ Ìï¥Îãπ Ïπ∏Ïóê ÎÇ†ÏßúÍ∞Ä ÏóÜÏúºÎ©¥ divÏóòÎ¶¨Î®ºÌä∏Îßå ÏÉùÏÑ±ÌïúÎã§.
          let dateEl = document.createElement("div");
          calendarBody.appendChild(dateEl);
        } else {
          // Ìï¥Îãπ Ïπ∏Ïóê ÎÇ†ÏßúÍ∞Ä ÏûàÏúºÎ©¥ divÏóòÎ¶¨Î®ºÌä∏ ÏÉùÏÑ± ÌõÑ Ìï¥Îãπ ÎÇ†Ïßú ÎÑ£Ïñ¥Ï£ºÍ∏∞
          let dateEl = document.createElement("div");
          let dataUl = document.createElement("ul")
          
          dateEl.textContent = dateSet;
          dateEl.setAttribute("class", dateSet);
          dateEl.setAttribute("id", `${today.format2()}-${dateSet}`);
          calendarBody.appendChild(dateEl);
          
          dateSet++;
        }
      }
      
    }
    // ÌòÑÏû¨ ÎÇ¥Í∞Ä ÏÑ†ÌÉùÌïú ÎÇ†ÏßúÍ∞Ä ÏûàÏúºÎ©¥ Ïù¥Ï†Ñ Îã¨, Îã§Ïùå Îã¨Î°ú ÎÑòÏñ¥Í∞ÄÎèÑ ÌôîÎ©¥Ïóê Î≥¥Ïó¨Ï£ºÍ∏∞ ÏúÑÌï¥ Ïç®Ï§å
    let clickedDate = document.getElementsByClassName(today.getDate());
    clickedDate[0].classList.add("active");
  }

  function removeCalendar() {
    let divEls = document.querySelectorAll(".calendar-body > div");
    for (let i = 0; i < divEls.length; i++) {
      divEls[i].remove();
    }
  }

  // ÏôºÏ™ΩÏóê ÌòÑÏû¨ ÎÇ†Ïßú ÏóÖÎç∞Ïù¥Ìä∏ Ìï¥Ï£ºÍ∏∞.
  function showMain() {
    const mainDay = document.querySelector(".main-day");
    const mainDate = document.querySelector(".main-date");
    const dayList = ["(Ïùº)", "(Ïõî)", "(Ìôî)", "(Ïàò)", "(Î™©)", "(Í∏à)", "(ÌÜ†)"];
    mainDate.innerHTML = `${today.getFullYear()}.${
      today.getMonth() + 1
    }.${today.getDate()}.`;
    mainDay.innerHTML = dayList[today.getDay()];
  }

  prevEl.addEventListener("click", function () {
    today = new Date(
      today.getFullYear(),
      today.getMonth() - 1,
      today.getDate()
    );
    removeCalendar();
    buildCalendar();
    resetInsert();
    redrawLi();
  });
  nextEl.addEventListener("click", function () {
    today = new Date(
      today.getFullYear(),
      today.getMonth() + 1,
      today.getDate()
    );
    removeCalendar();
    buildCalendar();
    resetInsert();
    redrawLi();
  });

  function currentDateget() {
    // format()ÏùÑ Ïù¥Ïö©Ìï¥ÏÑú ÌòÑÏû¨ ÎÇ†ÏßúÎ•º Î≥¥Í∏∞Ï¢ãÍ≤å Ï∂úÎ†•Ìï¥Ï£ºÍ∏∞ ÏúÑÌï¥ ÏÇ¨Ïö©.
    currentDate = today.format();
  }

  calendarBody.addEventListener("click", function (e) {
    let target = e.target;
    let eachDate = document.querySelectorAll(".calendar-body  > div");
    if (e.target.innerHTML === "") return;
    for (let i = 0; i < eachDate.length; i++) {
      eachDate[i].classList.remove("active");
    }
    target.classList.add("active");
    today = new Date(today.getFullYear(), today.getMonth(), target.innerHTML);
    showMain();
    currentDateget();
    redrawLi();
    resetInsert();
  });

  inputBtn.addEventListener("click", function (e) {
    e.preventDefault();
    let inputValue = inputBox.value;

    insertTodo(inputValue);
  });

  function insertTodo(text) {
    let todoObj = {
      todo: text,
    };

    if (!DATA[currentDate]) {
      DATA[currentDate] = [];
      DATA[currentDate].push(todoObj);
      console.log(DATA[currentDate]);
    } else {
      DATA[currentDate].push(todoObj);
      console.log(DATA[currentDate]);
    }
    const liEl = document.createElement("li");
    const spanEl = document.createElement("span");
    const delBtn = document.createElement("button");
    delBtn.innerText = "ÏÇ≠Ï†ú";
    delBtn.setAttribute("class", "del-data");
    spanEl.innerHTML = text;
    liEl.appendChild(spanEl);
    liEl.appendChild(delBtn);
    inputList.appendChild(liEl);
    liEl.setAttribute("id", DATA[currentDate].length);
    delBtn.addEventListener("click", delWork);
    liEl.addEventListener("dblclick", showTodo);
    // todoObjÏóê idÍ∞íÏùÑ 114Î≤à Ï§ÑÏóêÏÑú ÎÑ£Ïñ¥Ï£ºÎ©¥ DATA[currentDate].length Í∞íÏùÑ Ï∞æÏïÑÏò¨ Ïàò ÏóÜÍ∏∞ ÎïåÎ¨∏Ïóê pushÌï¥Ï§Ä ÌõÑ Ïóê Ï∂îÍ∞ÄÌïòÏó¨ localÏóê Ï†ÄÏû•ÌïúÎã§.
    todoObj.id = DATA[currentDate].length;
    save();
    inputBox.value = "";
  }

  function redrawLi() {
    // Îã§Î•∏ ÎÇ†ÏßúÎ•º ÌÅ¥Î¶≠ÌñàÏùÑÎïå Í∑∏ Ï†ÑÏóê ÏûëÏÑ±Ìïú totolistÎ™©Î°ùÏùÑ Î®ºÏ†Ä Îã§ ÏßÄÏö∞Í∏∞ ÏúÑÌï¥ liÏôÄ spanÏùÑ Ï∞æÏïÑÏôÄ forÎ¨∏ÏúºÎ°ú ÏßÄÏõåÏ£ºÍ≥† Îã§Ïãú Í∑∏Î†§Ï§ÄÎã§.
    let liEl = document.querySelectorAll("LI");
    for (let i = 0; i < liEl.length; i++) {
      inputList.removeChild(liEl[i]);
    }
    for (let todoList in DATA) {
      if (todoList === currentDate) {
        for (let i = 0; i < DATA[todoList].length; i++) {
          const liEl2 = document.createElement("li");
          const spanEl2 = document.createElement("span");
          const delBtn2 = document.createElement("button");
          
          delBtn2.innerText = "ÏÇ≠Ï†ú";
          delBtn2.setAttribute("class", "del-data");
          spanEl2.innerHTML = DATA[todoList][i].todo;
          liEl2.appendChild(spanEl2);
          liEl2.appendChild(delBtn2);
          inputList.appendChild(liEl2);
          liEl2.setAttribute("id", DATA[todoList][i].id);
          delBtn2.addEventListener("click", delWork);
          liEl2.addEventListener("dblclick", showTodo);

          

        }
      }
    }
  }

  // Îã§ÏùåÎã¨,Ïù¥Ï†ÑÎã¨ Îã§Î•∏ÎÇ†, Ï≤´ Î°úÎìú Îê† Îïå ÎßàÎã§ todo Î™©Î°ùÏù¥ ÏûàÏúºÎ©¥(ifÎ°ú Ï°∞Í±¥Î¨∏ Ï≤òÎ¶¨) Îã§ ÏßÄÏö∞Í≥† Îã§Ïãú Í∑∏Î†§Ï£ºÎäî Ìï®Ïàò
  function resetInsert() {
    let storeObj = localStorage.getItem(currentDate);
    if (storeObj !== null) {
      let liEl = document.querySelectorAll("LI");
      for (let i = 0; i < liEl.length; i++) {
        inputList.removeChild(liEl[i]);
      }
      // parse Ìï¥Ï£ºÍ∏∞ Ï†ÑÏóêÎäî localStorageÎäî stringÎßå Í∞ÄÏ†∏Ïò§ÎãàÍπå parseÌï¥Ï§ÄÎã§.
      const parsed = JSON.parse(localStorage.getItem(currentDate));
      // forEachÎ°ú ÏûëÏÑ±ÎêòÏûàÎäî Î™®Îì† todolistÏùò Ìï≠Î™©Îì§ÏùÑ ÎèåÎ©¥ÏÑú Î°úÏª¨Ïóê Ï†ÄÏû•ÎêòÏñ¥ ÏûàÎäî Î™©Î°ùÏùÑ ÌôîÎ©¥Ïóê ÎßåÎì§Ïñ¥Ï§ÄÎã§.
      parsed.forEach(function (todo) {
        if (todo) {
          let lili = document.createElement("li");
          let spanspan = document.createElement("span");
          let deldel = document.createElement("button");
          deldel.setAttribute("class", "del-data");
          deldel.innerText = "ÏÇ≠Ï†ú";
          lili.setAttribute("id", todo.id);
          spanspan.innerHTML = todo.todo;
          lili.appendChild(spanspan);
          lili.appendChild(deldel);
          inputList.appendChild(lili);
          deldel.addEventListener("click", delWork);
          lili.addEventListener("dblclick", showTodo);
        }
      });
    }
  }
  resetInsert();

  function delWork(e) {
    e.preventDefault();
    let delParentLi = e.target.parentNode;
    inputList.removeChild(delParentLi);
    // DATA[currentDate]Î•º filterÌï®ÏàòÎ•º Ïù¥Ïö©Ìï¥ todoÎ°ú ÎèåÎ©¥ÏÑú todoÏùò ÏïÑÏù¥ÎîîÍ∞íÍ≥º ÌòÑÏû¨ ÎÇ¥Í∞Ä ÎàÑÎ•∏ ÏïÑÏù¥ÎîîÍ∞íÏù¥ Í∞ôÏßÄ ÏïäÏùÄ Í≤ÉÏùÑ Î∞∞Ïó¥Ïóê Îã¥ÏïÑ Î¶¨ÌÑ¥Ìï¥Ï£ºÏñ¥ÏÑú
    // ÎÇ¥Í∞Ä ÏßÄÏö∞Í≥†Ïûê ÌïòÎäî ÏöîÏÜåÎ•º Î∫Ä ÎÇòÎ®∏ÏßÄ ÏöîÏÜåÎ•º Î∞∞Ïó¥Ïóê Îã¥ÏïÑ Î¶¨ÌÑ¥Ìï¥Ï§ÄÎã§.
    // Í∑∏ Î∞∞Ïó¥ÏùÑ Îã§Ïãú DATA[currentDate]Ïóê Ìï†ÎãπÌïòÏó¨ save();Î•º ÌÜµÌï¥ localStorageÏóê ÎÑ£Ïñ¥Ï§ÄÎã§.
    const cleanToDos = DATA[currentDate].filter(function (todo) {
      return todo.id !== parseInt(delParentLi.id);
    });
    DATA[currentDate] = cleanToDos;
    save();
  }

  function showTodo(e) {
    showList.style.display = "block";
    bgblack.style.display = "block";
    listText.textContent = e.target.textContent;
    createDate.textContent = currentDate;
  }

  closedBtn.addEventListener("click", function (e) {
    showList.style.display = "none";
    bgblack.style.display = "none";
  });

  function save() {
    localStorage.setItem(currentDate, JSON.stringify(DATA[currentDate]));
  }
};
